const videoBlockPopup = new Popup();
const videoBlocks = document.querySelectorAll('.js-video-block');

Array.from(videoBlocks).forEach(function(videoBlock) {
	videoBlock.addEventListener('click', function() {
		const videoSrc = videoBlock.dataset.videoSrc;

		videoBlockPopup
			.options({
				addClassNamePopup: 'popup_video'
			})
			.html(`<iframe width="0" height="0" frameborder="0" src="${videoSrc}" allowfullscreen></iframe>`)
			.show();
	});
});